@import './css/Modal.css';

ul li a {
color: white;
}

.background {
background-image: url('./images/bg.jpg');
background-size: cover;
background-repeat: no-repeat;
background-position: center center;
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
}

.header {
position: fixed;
top: 0;
left: 0;
right: 0;
background-color: rgba(0, 0, 0, 0.8);
z-index: 1;
display: flex;
align-items: center;
justify-content: space-between;
padding: 0 20px;
height: 60px;
}

.header ul {
list-style: none;
margin: 0;
padding: 0;
}

.header ul li {
display: inline-block;
margin-right: 10px;
}

.header ul li a {
color: white;
}

.content {
position: fixed;
top: 500px;
left: 50%;
transform: translate(-50%, -50%);
text-align: center;
color: white;
background-color: transparent;
border: none;
box-shadow: 0px 0px 5px black;
background: rgba(50, 40, 70, 0.9);
max-width: 800px;
margin: 0 auto;
padding: 50px;
}

.content-contact {
    position: absolute;
    top: 100px;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: white;
    background-color: transparent;
    border-style: unset;
    box-shadow: 0px 0px 5px black;
    background: rgba(50, 40, 70, 0.7);
    max-width: 800px;
    margin: 0 auto;
    padding: 50px;
    height: 800px;
}
  
